<template>
    <div class="show-value">
        <v-divider class="mt-2 mb-2"></v-divider>
        <div class="movie-sub-header mt-2">
            <router-link v-if="item.year" no-style :to="`/library/${item.librarySectionID}/?filter=year~${item.year}`"
                         class="ml-3 mr-5">
                {{ item.year }}
            </router-link>
            <span v-if="item.duration" class="ml-3 mr-5">{{ duration }}</span>
            <v-chip v-if="item.contentRating" :to="`/library/${item.librarySectionID}/?filter=contentRating~${item.contentRating}`"
                    class="ml-3 mr-5" small>{{ item.contentRating }}
            </v-chip>
            <div title="Critic rating" class="movie-sub-header" v-if="item.rating && !hideRatings">
                <span class="ml-3 mr-1">{{ item.rating * 10 }}% </span>
                <div class="tomato mr-5"></div>
            </div>
            <div title="Audience rating" class="movie-sub-header" v-if="item.audienceRating && !hideRatings">
                <span class="mr-1">{{ item.audienceRating * 10 }}% </span>
                <div class="popcorn mr-5"></div>
            </div>
        </div>
        <v-divider class="mt-2 mb-2"></v-divider>
    </div>
</template>

<script>
import Utils from "@/js/Utils";

export default {
    name: "DataHeader",
    props: {
        item: {
            type: Object,
            default: null,
        },
        hideRatings: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        duration() {
            return Utils.niceTime(new Date(this.item.duration));
        },
    },
}
</script>

<style scoped>
.movie-sub-header {
    display: flex;
    align-items: center;
}

.show-value {
    font-size: 14px;
    font-weight: bold;
}

.tomato {
    background-image: url("data:image/svg+xml;base64,");
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
}

.popcorn {
    background-image: url("data:image/svg+xml;base64,");
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
}
</style>